package com.den3000.kmpwaystoaurora.shared

import app.cash.sqldelight.SuspendingTransacterImpl
import app.cash.sqldelight.db.AfterVersion
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlDriver
import app.cash.sqldelight.db.SqlSchema
import com.den3000.kmpwaystoaurora.Database
import com.den3000.kmpwaystoaurora.LaunchQueries
import com.den3000.kmpwaystoaurora.ProgrammerQueries
import kotlin.Long
import kotlin.Unit
import kotlin.reflect.KClass

internal val KClass<Database>.schema: SqlSchema<QueryResult.AsyncValue<Unit>>
  get() = DatabaseImpl.Schema

internal fun KClass<Database>.newInstance(driver: SqlDriver): Database = DatabaseImpl(driver)

private class DatabaseImpl(
  driver: SqlDriver,
) : SuspendingTransacterImpl(driver), Database {
  override val launchQueries: LaunchQueries = LaunchQueries(driver)

  override val programmerQueries: ProgrammerQueries = ProgrammerQueries(driver)

  public object Schema : SqlSchema<QueryResult.AsyncValue<Unit>> {
    override val version: Long
      get() = 1

    override fun create(driver: SqlDriver): QueryResult.AsyncValue<Unit> = QueryResult.AsyncValue {
      driver.execute(null, """
          |CREATE TABLE launch (
          |    flightNumber INTEGER NOT NULL,
          |    missionName TEXT NOT NULL,
          |    details TEXT,
          |    launchSuccess INTEGER DEFAULT NULL,
          |    launchDateUTC TEXT NOT NULL,
          |    patchUrlSmall TEXT,
          |    patchUrlLarge TEXT,
          |    articleUrl TEXT
          |)
          """.trimMargin(), 0).await()
      driver.execute(null, """
          |CREATE TABLE programmer (
          |  programmer_number INTEGER PRIMARY KEY NOT NULL,
          |  full_name TEXT NOT NULL
          |)
          """.trimMargin(), 0).await()
      driver.execute(null, "CREATE INDEX programmer_full_name ON programmer(full_name)", 0).await()
      driver.execute(null, """
          |INSERT INTO programmer (programmer_number, full_name)
          |VALUES (1, 'Charles Babbage'),
          |(2, 'Brendan Eich'),
          |(3, 'Tim Berners-Lee'),
          |(4, 'Linus Torvalds'),
          |(5, 'Brian Kernighan')
          """.trimMargin(), 0).await()
    }

    override fun migrate(
      driver: SqlDriver,
      oldVersion: Long,
      newVersion: Long,
      vararg callbacks: AfterVersion,
    ): QueryResult.AsyncValue<Unit> = QueryResult.AsyncValue {
    }
  }
}
