package com.den3000.kmpwaystoaurora

import app.cash.sqldelight.Query
import app.cash.sqldelight.SuspendingTransacterImpl
import app.cash.sqldelight.db.SqlDriver
import kotlin.Any
import kotlin.Boolean
import kotlin.Long
import kotlin.String

public class LaunchQueries(
  driver: SqlDriver,
) : SuspendingTransacterImpl(driver) {
  public fun <T : Any> selectAllLaunchesInfo(mapper: (
    flightNumber: Long,
    missionName: String,
    details: String?,
    launchSuccess: Boolean?,
    launchDateUTC: String,
    patchUrlSmall: String?,
    patchUrlLarge: String?,
    articleUrl: String?,
  ) -> T): Query<T> = Query(-1_626_172_331, arrayOf("launch"), driver, "Launch.sq",
      "selectAllLaunchesInfo", """
  |SELECT launch.flightNumber, launch.missionName, launch.details, launch.launchSuccess, launch.launchDateUTC, launch.patchUrlSmall, launch.patchUrlLarge, launch.articleUrl
  |FROM launch
  """.trimMargin()) { cursor ->
    mapper(
      cursor.getLong(0)!!,
      cursor.getString(1)!!,
      cursor.getString(2),
      cursor.getBoolean(3),
      cursor.getString(4)!!,
      cursor.getString(5),
      cursor.getString(6),
      cursor.getString(7)
    )
  }

  public fun selectAllLaunchesInfo(): Query<Launch> = selectAllLaunchesInfo { flightNumber,
      missionName, details, launchSuccess, launchDateUTC, patchUrlSmall, patchUrlLarge,
      articleUrl ->
    Launch(
      flightNumber,
      missionName,
      details,
      launchSuccess,
      launchDateUTC,
      patchUrlSmall,
      patchUrlLarge,
      articleUrl
    )
  }

  public suspend fun insertLaunch(
    flightNumber: Long,
    missionName: String,
    details: String?,
    launchSuccess: Boolean?,
    launchDateUTC: String,
    patchUrlSmall: String?,
    patchUrlLarge: String?,
    articleUrl: String?,
  ) {
    driver.execute(209_912_299, """
        |INSERT INTO launch(flightNumber, missionName, details, launchSuccess, launchDateUTC, patchUrlSmall, patchUrlLarge, articleUrl)
        |VALUES(?, ?, ?, ?, ?, ?, ?, ?)
        """.trimMargin(), 8) {
          bindLong(0, flightNumber)
          bindString(1, missionName)
          bindString(2, details)
          bindBoolean(3, launchSuccess)
          bindString(4, launchDateUTC)
          bindString(5, patchUrlSmall)
          bindString(6, patchUrlLarge)
          bindString(7, articleUrl)
        }.await()
    notifyQueries(209_912_299) { emit ->
      emit("launch")
    }
  }

  public suspend fun removeAllLaunches() {
    driver.execute(1_121_767_039, """DELETE FROM launch""", 0).await()
    notifyQueries(1_121_767_039) { emit ->
      emit("launch")
    }
  }
}
