package com.den3000.kmpwaystoaurora

import app.cash.sqldelight.SuspendingTransacter
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlDriver
import app.cash.sqldelight.db.SqlSchema
import com.den3000.kmpwaystoaurora.shared.newInstance
import com.den3000.kmpwaystoaurora.shared.schema
import kotlin.Unit

public interface Database : SuspendingTransacter {
  public val launchQueries: LaunchQueries

  public val programmerQueries: ProgrammerQueries

  public companion object {
    public val Schema: SqlSchema<QueryResult.AsyncValue<Unit>>
      get() = Database::class.schema

    public operator fun invoke(driver: SqlDriver): Database = Database::class.newInstance(driver)
  }
}
