package com.den3000.kmpwaystoaurora

import app.cash.sqldelight.Query
import app.cash.sqldelight.SuspendingTransacterImpl
import app.cash.sqldelight.db.SqlDriver
import kotlin.Any
import kotlin.Long
import kotlin.String

public class ProgrammerQueries(
  driver: SqlDriver,
) : SuspendingTransacterImpl(driver) {
  public fun <T : Any> selectAll(mapper: (programmer_number: Long, full_name: String) -> T):
      Query<T> = Query(-2_030_631_933, arrayOf("programmer"), driver, "Programmer.sq", "selectAll",
      """
  |SELECT programmer.programmer_number, programmer.full_name
  |FROM programmer
  """.trimMargin()) { cursor ->
    mapper(
      cursor.getLong(0)!!,
      cursor.getString(1)!!
    )
  }

  public fun selectAll(): Query<Programmer> = selectAll { programmer_number, full_name ->
    Programmer(
      programmer_number,
      full_name
    )
  }
}
